import React, {useEffect, useState} from 'react'

import Main from './../components/UI/Main/Main'
import Grid from './../components/UI/Grid'

import Twitter from './../components/UI/Main/DoubleDiv'
import Stats from './../components/Table/Stats'
import HolderCount from './../components/Table/HolderCount'
import HolderSupply from './../components/Table/HolderSupply'
import HolderSearch from './../components/Form/HoldersSearch'


const Dashboard = (props) => {
    const [HolderTableState, setHolderTableState] = useState(true)

    const ChangeHolderTableState = () => {
        setHolderTableState(prev => prev = !prev)
    }

    return (
        <Main>
            <Grid GridBox="high-thicc">
                <h2>SafeMoon Stats</h2>
                <Stats/>
            </Grid>
            <Grid GridBox="high-thicc">
                <h2 className="clickable border-radius" onClick={ChangeHolderTableState}>{HolderTableState ? "Holder Distribution" : "Supply Distribution"}</h2>
                {HolderTableState && <HolderCount/>}
                {!HolderTableState && <HolderSupply/>}
                <HolderSearch />
            </Grid>
            <Grid GridBox="full">
                <Twitter/>
            </Grid>
        </Main>
    )
}

export default Dashboard
